









import Vue from 'vue';
import {Component, Watch,Prop} from 'vue-property-decorator';
@Component
export default class FormItem extends Vue {
  input = '';
  @Prop({default:''})readonly fileName!:String;
  @Prop()placeholder?:string;

  @Watch('input')
  update() {
    this.$emit('update:value', this.input);
  }
};
