















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class Tags extends Vue {
  get dataSource() {
   return  this.$store.state.dataSource.tags;
  }
  selectedTags: String[] = [];

  toggle(tag: string) {
    const index = this.selectedTags.indexOf ( tag );
    index >= 0 ? this.selectedTags.splice ( index, 1 ) : this.selectedTags.push ( tag );
    this.$store.commit( 'updateSelectedTags', this.selectedTags );
  };

  create() {
    let name = window.prompt ( '请输入标签名' );
    while (name === '') {
      name = window.prompt ( '请输入标签名' );
    }
    if (name !== null) {
      this.$store.commit( 'updateTags', name );
      this.$store.commit("save")
    }
  }
}
